@import url(https://fonts.googleapis.com/css2?family=Nanum+Myeongjo&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.long {
  max-width: 400px;
}

.to-map {
  max-width: 400px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.schedule {
  font-family: "Nanum Myeongjo", serif;
  margin-bottom: 2rem;
}

.schedule ul {
  line-height: 2rem;
  text-align: left;
}

.invite {
  max-width: 20rem;
  line-height: 1.5rem;
  font-family: "Nanum Myeongjo", serif;
  margin-bottom: 2rem;
}

.map {
  line-height: 2rem;
}

.map h2 {
  font-family: "Nanum Myeongjo", serif;
}

.to-map {
  max-width: 350px;
}

.line {
  size: 3px;
  width: 10rem;
  color: #eaeaea;
}

.copyBtn {
  width: 13rem;
  height: 4rem;
  border: none;
  background-color: white;
  font-weight: bolder;
  display: block;
  text-align: center;
  margin: 3rem auto;
  top: 5rem;
}

